import React from "react";
import CryptoJS from "crypto-js";

// Function to get the value of a cookie by its name
export const getCookieValue = (name) => {
    const cookies = document.cookie.split(';');
    for (let i = 0; i < cookies.length; i++) {
        const cookie = cookies[i].trim();
        if (cookie.startsWith(name + '=')) {
            return cookie.substring(name.length + 1);
        }
    }
    return null;
};

let decryptedToken;
//const encryptedToken = getCookieValue("token");
const encryptedToken = localStorage.getItem("token");
const encryptedObj_id = getCookieValue("obj_id");
const exDays = 24 * 60 * 60 * 60;
const term = "2024-04-10";
const Key = "e3251447069f7b89c517b647b448e542598c0d41e76edfd0a7ad4f802e447718";
// const secretKey = crypto.lib.WordArray.random(256 / 8).toString(crypto.enc.Hex);

if (encryptedToken) {
    const bytes = CryptoJS.AES.decrypt(encryptedToken, Key);
    decryptedToken = bytes.toString(CryptoJS.enc.Utf8);

} else {
    decryptedToken = null
}

