import React, { useState, useRef, useEffect } from "react";
import { Stack, Typography, Checkbox, Divider, } from "@mui/material";
import UIText from "../../utils/TextResources";
import Search from "../Search/Search";
import Loader from "../../Loader/Loader";
import useLoader from "../../utils/UseLoader";
import Suggestions from "../Suggestions/Suggestions";
import SelectedSuggestion from "../Suggestions/SelectedSuggestion";
import UserInfo from "../../utils/UsersInfo";
import { Token } from "../../utils/token";
import { GetMessage } from "../../services/GetMessage";
import { SuggestionList } from "../../services/SuggestionList";
import { SyncMessage } from "../../services/SyncMessage";
import { SyncAttachments } from "../../services/SyncAttachments";
import { UnSyncMessage } from "../../services/UnSyncMessage";

const Header = () => {
    const loading = useLoader();
    const apiCalled = useRef(false);
    const [isLoading, setIsLoading] = useState(loading);
    const [showSearch, setShowSearch] = useState(loading);
    const [suggestionData, setSuggestionData] = useState();
    const [selectedObjectId, setSelectedObjectId] = useState(null);
    const [showSelectedSuggestion, setShowSelectedSuggestion] = useState();
    const { senderName, senderEmail, ccEmails, toEmails, mailBody, DateTime, message_id, thread_id, title, attachments, attachmentNames } = UserInfo();
    const [apiSync, setApiSync] = useState(false);
    const [syncMsg, setSyncMsg] = useState(false);
    const [syncMsgError, setSyncMsgError] = useState(false);
    const [getMessagesData, setGetMessagesData] = useState();
    const accessToken = Token();

    useEffect(() => {
        if (thread_id && accessToken && senderName && senderEmail && ccEmails && toEmails && mailBody && DateTime && message_id && !apiCalled.current) {
            setIsLoading(true);

            GetMessage(thread_id, accessToken, (error, data) => {
                if (error) {
                    // console.log(error);
                    if (error == 404) {
                        setApiSync(true);
                        setIsLoading(false);
                    }
                } else {
                    setGetMessagesData(data);
                    const messageExists = data.messages.some(message => message.message_id == message_id);
                    console.log("Message exists:", messageExists);
                    var objid = data.id;
                    console.log(objid);
                    // Message with the ID exists 
                    if (messageExists == true) { // This is for testing messageExists == false
                        setSyncMsg(true);
                        setShowSelectedSuggestion(true);
                        setSelectedObjectId(objid);
                    } else {
                        // Message with the ID does not exist
                        handleSelectedObjectId(objid);
                        setSyncMsg(false);
                    }
                    setApiSync(false);
                    setIsLoading(false);
                }
            });
            apiCalled.current = true;
        } else {
            console.log("All parameters not here");
        }

    }, [accessToken, senderName, senderEmail, ccEmails, toEmails, mailBody, DateTime, message_id, thread_id, title, syncMsg]);

    const handleSyncMessage = () => {
        setIsLoading(true);
        if (syncMsg) {
            // Call UnSyncMessage if checkbox is unchecked
            UnSyncMessage(accessToken, thread_id, (error, data) => {
                if (error) {
                    console.log(error);
                    setIsLoading(false);
                } else {
                    console.log("unSync success", data);
                    apiCalled.current = false;
                    setSyncMsg(false);
                    setIsLoading(false);

                    setShowSearch(false);
                    setSelectedObjectId(null);
                    setShowSelectedSuggestion(false);
                }
            });
        } else {
            // Show search and suggestion screens when user check the checkbox
            setShowSearch(true);

            SuggestionList(accessToken, senderName, senderEmail, ccEmails, toEmails, mailBody, DateTime, message_id, thread_id, title, attachmentNames, apiCalled, (error, data) => {
                if (error) {
                    console.log(error);
                    setIsLoading(false);

                    // if (error.status == 500) {
                    //     setSyncMsgError("Server Error");
                    //     setIsLoading(false);
                    // } else {
                    //     let errorMsg = error.responseText;
                    //     const removeQuotes = errorMsg.replace(/['"]/g, ''); // Remove both single and double quotes
                    //     setSyncMsgError(removeQuotes);
                    //     setIsLoading(false);
                    // }
                    setSyncMsg(false);
                } else {
                    console.log("Suggestion Callback", data);
                    setSuggestionData(data);
                    setSyncMsg(true);
                    setIsLoading(false);
                }
            });
        }
    };

    const handleSelectedObjectId = (objId) => {
        setIsLoading(true);
        setSelectedObjectId(objId);
        setShowSelectedSuggestion(true);
        // console.log(objId);

        // Call SyncMessage API
        SyncMessage(accessToken, objId, senderName, senderEmail, ccEmails, toEmails, mailBody, DateTime, message_id, thread_id, title, (error, data) => {
            if (error) {
                console.log(error);
                if (error.status == 404) {
                    setSyncMsgError("The requested resource was not found on this server");
                    setSelectedObjectId(null);
                    setIsLoading(false);
                } else {
                    let errorMsg = error.responseText;
                    const removeQuotes = errorMsg.replace(/['"]/g, ''); // Remove both single and double quotes
                    setSyncMsgError(removeQuotes);
                    setSelectedObjectId(null);
                    setIsLoading(false);
                }
            } else {
                console.log("Sync Success", data);
                apiCalled.current = false;
                if (syncMsg == true) {
                    setSyncMsg(false);
                } else {
                    setSyncMsg(true);
                }
                setIsLoading(false);

                // SyncAttachments if msg synced
                SyncAttachments(accessToken, attachments, message_id, (error, data) => {
                    if (error) {
                        console.log(error);
                        setIsLoading(false);
                    } else {
                        console.log("syncAttachment Success", data);
                        setIsLoading(false);
                    }
                });
            }
        });
    };

    return (
        <>
            {isLoading && (
                <Loader
                    loaderOverlay={{
                        position: 'fixed',
                        top: 0,
                        left: 0,
                        width: '100%',
                        height: '100%',
                        backgroundColor: 'rgba(255, 255, 255, 0.8)',
                        display: 'flex',
                        justifyContent: 'center',
                        alignItems: 'center',
                        zIndex: 9999,
                    }}
                    loader={{ size: "40px" }}
                />
            )}

            <Divider />
            <Stack
                padding="8px"
                flexDirection="column"
                textAlign="left"
                marginTop="8px"
                spacing={2}
            >
                <Stack sx={{ display: "flex", flexDirection: "row" }}>
                    <Checkbox
                        color="primary"
                        checked={syncMsg}
                        onChange={handleSyncMessage}
                        sx={{
                            marginTop: "-10px",
                            marginLeft: "-10px"
                        }}
                    />
                    <Typography variant="body2" paddingTop="2px">{UIText.header.track}</Typography>
                </Stack>
            </Stack>
            <Divider />

            {showSearch && !selectedObjectId && (
                <>
                    <Search handleSelectedObjectId={handleSelectedObjectId} />
                    <Suggestions suggestionData={suggestionData} handleSelectedObjectId={handleSelectedObjectId} />
                </>
            )}

            {syncMsg && !syncMsgError && selectedObjectId && showSelectedSuggestion && (<SelectedSuggestion Getmessages={getMessagesData} />)}

            {/* Show syncMsgError */}
            {syncMsgError && (<Typography sx={{ padding: "8px", fontSize: "12px", color: "red" }}>{syncMsgError}</Typography>)}
        </>
    );
};

export default Header;
