import { constants } from "../utils/Constants";

export const SuggestionList = (accessToken, senderName, senderEmail, ccEmails, toEmails, mailBody, DateTime, message_id, thread_id, title, attachmentNames, apiCalled, callback) => {
    if (senderName && senderEmail && ccEmails && toEmails && mailBody && DateTime && message_id && thread_id && title && apiCalled) {
        var settings = {
            "url": `${constants.BASE_URL}/api/v1/email/suggest/`,
            "method": "POST",
            "timeout": 0,
            "headers": {
                "Content-Type": "application/json",
                "Authorization": `JWT ${accessToken}`
            },
            // For static data 
            // "data": JSON.stringify({
            //     "title": "Late part",
            //     "thread_id": "THREAD_ID1",
            //     "messages": [
            //         {
            //             "message_id": "MSG_ID_1",
            //             "from_email": "ghemmelgarnunique@kennsedytank.com",
            //             "from_name": "Arya Khaligh",
            //             "to": [
            //                 "Aamir@axya.co"
            //             ],
            //             "cc": [],
            //             "body": "This is the PO number: 00010319",
            //             "attachments": [
            //                 "file1.pdf",
            //                 "file2.pdf"
            //             ],
            //             "date": "2024-04-10T15:30:00Z"
            //         }
            //     ]
            // }),

            // For dynamic data
            "data": JSON.stringify({
                "title": title,
                "thread_id": thread_id,
                "messages": [
                    {
                        "message_id": message_id,
                        "from_email": senderEmail,
                        "from_name": senderName,
                        "to": toEmails,
                        "cc": ccEmails,
                        "body": mailBody,
                        "attachments": attachmentNames,
                        "date": DateTime,
                    }
                ]
            }),
        };

        $.ajax(settings)
            .done(function (response) {
                // console.log(response);
                callback(null, response);
            })
            .fail(function (error) {
                console.error(error);
                callback(error, null);
            });
    }
}