/* eslint-disable no-undef */
/* eslint-disable react/prop-types */
/* eslint-disable prettier/prettier */
import { constants } from '../utils/Constants';

export const SyncMessage = (accessToken, objId, senderName, senderEmail, ccEmails, toEmails, mailBody, DateTime, message_id, thread_id, title, callback) => {
    if (accessToken && objId && senderName && senderEmail && ccEmails && toEmails && mailBody && DateTime && message_id && thread_id && title) {
        var settings = {
            "url": `${constants.BASE_URL}/api/v1/email/sync/${thread_id}/`,
            "method": "POST",
            "timeout": 0,
            "headers": {
                "Authorization": `JWT ${accessToken}`,
                "Content-Type": "application/json"
            },
            "data": JSON.stringify({
                "obj_id": objId,
                "emails": {
                    "title": title,
                    "thread_id": thread_id,
                    "messages": [
                        {
                            "message_id": message_id,
                            "from_email": senderEmail,
                            "from_name": senderEmail,
                            "to": toEmails,
                            "cc": ccEmails,
                            "body": mailBody,
                            "date": DateTime
                        }
                    ]
                }
            }),
        };

        $.ajax(settings)
            .done(function (response) {
                // console.log(response);
                callback(null, response);
            })
            .fail(function (error) {
                console.error(error);
                callback(error, null);
            });
    }
}

